/**
 *
 * 管理画面編集用scss
 * .post-type-post で投稿を指定
 *
 **/

 /* デフォルト投稿編集画面のスタイル */
.post-type-post .editor-styles-wrapper h2 {
  font-size: rem(20);
  line-height: 1;
  letter-spacing: calc( 2 / 24 * 1rem);
  padding: rem(5) 0 rem(5) rem(9);
  position: relative;
}
.post-type-post .editor-styles-wrapper h2::before {
  position: absolute;
  content: '';
  display: block;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background: #C4C4C4;
  width: rem(3);
  height: 100%;
}

.post-type-post .editor-styles-wrapper h3{
  font-size: rem(18);
  letter-spacing: calc( 2 / 18 * 1rem);
  display: inline-block;
  position: relative;
}

.post-type-post .editor-styles-wrapper h3::after{
  position: absolute;
  content: '';
  display: block;
  width: 100%;
  height: rem(3);
  background: linear-gradient(to left, #FFF, #FFF 67%, #FFEA2E 67%, #FFEA2E 100%);
  left: 0;
  bottom: rem(-3);
}

.post-type-post .editor-styles-wrapper ul,
.post-type-post .editor-styles-wrapper ol {
  // color: red;
}